body{
  background-color: #282c34;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App {
  text-align: center;
  background-color: #282c34;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.champCard{background-color: #2a303b; width:20%; margin: 1%;display:flex; flex-direction: column; justify-content: flex-start; text-align: left; border-radius: 5px; padding: 2em;}
.champCard img{width: 100%;} 

@media screen and (max-width: 800px){
  .champCard{width: 100%;}
}